	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 1 - Danger imminent","texte":{"childMarkdownRemark":{"html":"<p>Je suis assis sur la terrasse d’un café où j’aime bien aller après l’école, principalement pour regarder les gens passer et m’interroger sur ce qui arrive dans leur vie respective. C’est sûrement plus intéressant que ce qui se passe dans ma vie. Je finis de boire mon café et mange la dernière bouchée de mon sandwich avant de me lever et d’aller payer mon repas. À cette heure, mon père sera sûrement parti à son travail. Je paie en saluant la caissière, qui est toujours aussi radieuse et souriante. Peut-être qu’un jour je lui proposerais de sortir faire une activité.</p>\n<p>Je sors du café et prend la route vers chez moi. Quelques coins de rue plus tard, la lumière tombe rouge. Je regarde rapidement et commence à traverser la rue. J’entends un klaxon suivi d’un crissement de pneu. J’ai à peine le temps de tourner la tête pour voir le visage de la personne qui allait sans doute me tuer dans cet accident inévitable. Je ferme les yeux, j’ai l’impression de voir ma vie défiler à toute vitesse; les gamins qui m’intimident à l’école, mon père qui s’amuse à me frapper lorsqu’il en a envie ou lorsqu’il n’est simplement pas d’humeur, la belle Cassia du café à qui je ne pourrai jamais proposer de sortir. Après quelques secondes, j’ouvre les yeux, je ne suis pas mort. Enfin, je ne crois pas, mes deux mains sont sur le capot de la voiture et le chauffeur me regarde; il est encore plus terrifié qu’avant l’impact. J’ai l’impression que ce ne sont pas mes mains simplement à la grosseur qu’elles ont. Je regarde mes bras, oui, c’est bien mes bras, mais pourquoi ont-ils doublé, voir triplé de grosseur? Je sens mon corps reprendre des proportions normales. Je titube un peu et tombe sur les fesses. Je ferme les yeux et m’évanouis.</p>\n<p>Étrangement, j’entends l’ambulance arriver et quelques passants s’approcher et me demander si je vais bien. Je sens les ambulanciers prendre mon pouls et vérifier si je respire. Ils me placent sur un brancard. J’entends certains passant s’écrier des choses du genre «Il a doublé de grosseur et a repris sa forme initiale après! » ou bien « C’est un monstre évadé du zoo! ». Les ambulanciers leur demandent de rester calme. Un homme s’écrie qu’il a tout filmé. Je sens qu’on me fait monter dans l’ambulance et qu’on ferme les portes.</p>\n<p>Quelques longues minutes plus tard, j’arrive à ouvrir les yeux et alors qu’un des ambulanciers me regarde.</p>\n<p>Ambulancier: Pouvez-vous nous expliquer ce qui c’est passé?<br>\nTim: Je n’en sais rien, j’ai vu une voiture arriver vers moi et puis plus rien.<br>\nAmbulancier: Ne vous inquiétez pas monsieur, nous vous conduisons à l’hôpital pour nous assurer que vous n’avez rien.<br>\nTim: Je n’aime pas les hôpitaux…<br>\nAmbulancier: Vous n’y resterez pas longtemps, je vous le promet.</p>\n<p>J’essaie m’examiner pour savoir si je vais bien. J’arrive à bouger les pieds et les mains, mais je suis attaché au brancard.</p>\n<p>Tim: Vous pouvez me détacher, s’il vous plaît?<br>\nAmbulancier: Vous arrivez à bouger vos mains et vos pieds?<br>\nTim: Oui, je n’ai aucun mal à faire ça.<br>\nAmbulancier: Des difficultés à respirer?<br>\nTim: Je ne crois pas non, peut-être à cause de la sangle?</p>\n<p>L’ambulancier me détache gentiment et je me redresse lentement. Je m'assois et le regarde.</p>\n<p>Ambulancier: Comment vous appelez-vous?<br>\nTim: Tim Allam.<br>\nAmbulancier: Vous vous souvenez de la date?<br>\nTim: Oui, nous sommes le Venaol 12. Il commence à faire une température plus douce et les terrasses des restaurants sont réinstallées.<br>\nAmbulancier: Exacte. C’est possible que l’on vous repose ces questions plus tard, pour nous assurer que vous ne souffrez pas de commotion.<br>\nTim: Ok...</p>\n<p>L’ambulancier regarde par dessus son épaule, sans doute pour voir si nous arrivons bientôt à l’hôpital. Je soupire et regarde mes mains. Je n’ai aucune marque; c’est vraiment étrange. Normalement, j’aurais des blessures aux mains, c’est avec elles que j’ai stoppé la voiture.</p>\n<p>L’ambulance s’arrête et je tourne la tête pour voir l’hôpital. L’ambulancière sort du véhicule et vient ouvrir les portes. Elle me regarde, surprise que je sois réveillé. L’ambulancier m’aide à descendre et on m’apporte une chaise roulante.</p>\n<p>Tim: Je peux marcher, merci…<br>\nAmbulancier: Nous suivons la procédure, Tim. Suis-nous.</p>\n<p>Je suis l’ambulancier, il me conduit au triage. Je remplis des papiers, donne mes informations personnelles et ensuite je décris ce qui s’est passé. La seule chose que je sus expliquer fut un truc du genre « Je me suis fait frapper par une voiture, mais je ne sais pas pourquoi, j’arrive encore à marcher ». La préposée à la réception me regarde en levant un sourcil; normalement leur travail inclut de ne pas porter de jugement.</p>\n<p>Je suis à nouveau l’ambulancier qui me conduit à une salle afin qu’un docteur examine les blessures que je pourrais avoir. Après une demi-heure d’attente, un docteur arrive enfin. Il procède à une vérification et remarque des bleus sur mes bras et un peu dans le dos. J’essaie de lui expliquer que ce n’est pas suite à l’accident et ce sans avouer que les blessures sont causées par mon père. Le docteur semble se décourager de mon silence et de mon explication flou. Il finit ses vérifications et me prescrit une crème contre les ecchymoses. Je prends le papier.</p>\n<p>Tim: Merci docteur.<br>\nDocteur: Prenez soin de vous et peu importe la raison de vos contusions, ne vous laissez pas faire.<br>\nTim: D’accord, docteur.</p>\n<p>Je me lève et replace mon chandail. Le docteur quitte et va appeller le prochain patient. Je quitte l'hôpital et reprend la route vers chez moi. À cette heure, il est clair que mon père ne sera pas présent. Une heure plus tard, j’arrive enfin chez moi, mon père est parti comme je l'espérais. Une fois arrivé, je vais prendre quelque chose à manger et me réfugie dans ma chambre. J’observe ma main pendant que je mange. J’ai encore de la difficulté à comprendre ce qui se passe avec moi et je contracte un peu ma main. Il faut que j’essaie des choses pour reproduire ce qui s’est passé, mais peut-être pas quelque chose d’aussi bruyant qu’une voiture qui me fonce dessus.</p>\n<p>Je réfléchis un peu et sors de ma chambre pour aller au salon. Je m’installe sur le divan et allume la télé sur le poste de nouvelles. Je fixe la télé sans trop y porter d’attention, absorbé par ma réflexion. Je remarque qu’il parlait de l’accident de voiture dont j’ai été victime. La vidéo y est même montrée. La vidéo est floue et bouge beaucoup, mais on voit néanmoins bien ce qui se passe. Une voiture qui fonce sur moi qui traversait sur une lumière rouge. Je me vois me tourner vers la voiture puis le haut de mon corps double de grosseur pour stopper le véhicule. L’instant d’après, j’avais repris une taille normale et je m’étais évanoui.</p>\n<p>Tim: Merde! Si quelqu’un me reconnait, je suis foutu! Je vais me faire découper en rondelle pour servir d’expérience.</p>\n<p>Je commence à paniquer. Qu’est-ce que je vais faire? Je me lève et sens mon corps changer un peu de forme, en fait, je sens mon corps grossir. Je regarde mes mains et mes bras pour constater qu’ils sont plus gros et bien plus musclés. C’est la montée d’adrénaline qui déclenche ça, peut-être? Je prends la télécommande, elle a l’air toute petite dans ma main maintenant. J’appuie sur le bouton de la télécommande un peu trop fort. La télé se ferme, mais j’entends le plastique craquer sous mon mouvement. Je l’ai sans doute brisée. Je pose la télécommande et retourne à ma chambre pour tenter de me calmer. Je dois me contrôler à chaque fois que j’ai l’intention de toucher quelque chose si je veux éviter de tout briser. Je m’allonge sur mon lit et soupire, essayant de me calmer. Que ferais-je si quelqu’un me reconnait? Non, c’est impossible que quelqu’un m’identifie, je suis pratiquement invisible. Je prends mon oreiller, la pose sur mon visage et hurle dedans pour me défouler. Une fois calmé, je la retire et regarde mes bras, ils avaient repris une taille normale. Je prends mon cellulaire et regarde si quelqu’un m’a écrit. Rien.</p>\n<p>Je le déverrouille et commence à jouer à un des jeux installés pour faire passer le temps. Après quelques minutes, je ferme l’application et retourne voir les nouvelles depuis mon cellulaire. J’essaie de retrouver celle qui parle de mon accident, mais impossible de la revoir. C’est étrange tout ça. Je continue de chercher et je trouve finalement quelque chose qui parle de l’accident indiquant que la vidéo est truquée.</p>\n<p>Tim: Sérieux!? Une vidéo truquée! C’est bien ma chance! Je ne sais pas qui ou quoi a décidé que c’était une vidéo truquée, mais c’est bien à mon avantage.</p>\n<p>Je souris un peu et me place bien dans mon lit. J’espère quand même que ça restera considéré comme une vidéo truquée.</p>"}},"chapitreAvant":null,"chapitreApres":"chapitre-2-rencontre","nomRoman":"../le-monstre-de-calpeo","slug":"chapitre-1-danger-imminent"}},"pathContext":{"slug":"chapitre-1-danger-imminent"}}

